body {
	position: relative;
	margin: 0 auto;
	width: 100%;
	font-family: 'Roboto', sans-serif;
	padding-top:100px;
	padding-bottom:60px;
	background: #FDFFF2;
}
.Header {
    position: fixed;
    top: 0;
    left: 0;
	text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.3em;
    width: 100%;
    line-height: 100px;
    font-size: 17px;
    z-index: 1000;
	background: #FDFFF2;
	color: #5F6060;
}
.About {
    position: relative;
	background: #1E1F21;
    font-family: 'Consolas', sans-serif;
    padding-top: 50px;
    padding-bottom: 70px;
}

.About h1 {
    margin: 0 auto;
    width: 800px;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
	color: #FDFFF2;
    line-height: 20px;
    padding-bottom: 40px;
}

.About p {
    margin: auto;
    width: 500px;
    font-size: 14px;
    font-weight: normal;
	color: #FDFFF2;
    line-height: 20px;
    padding-left: 5px;
    padding-right: 5px;
}

.About p span {
    color: rgb(197, 175, 114);
}
.Work {
    position: relative;
	background: #87515D;
    padding-top: 50px;
    padding-bottom: 70px;
}

.Work h1 {
    margin: 0 auto;
    width: 800px;
    font-size: 14px;
    font-family: 'Karla', sans-serif;
    font-weight: normal;
    text-align: center;
	color: #FDFFF2;
    line-height: 20px;
    padding-bottom: 40px;
}

.Work p {
    margin: auto;
    width: 500px;
    font-size: 14px;
    font-family: 'Karla', sans-serif;
	color: #FDFFF2;
    line-height: 20px;
    padding-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
}

.Work p span {
    color: #27182D;
}
.Footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    text-transform: uppercase;
    width: 100%;
    height: 60px;
    font-size: 12px;
	background: #FDFFF2;
    color: #5F6060;
    padding-top: 20px;
    line-height: 20px;
}

.Footer .widthHolder {
    max-width: 900px;
    margin: 0 auto;

}

.Footer .widthHolder .copyright {
    float: left;
}

.Footer .widthHolder .social {
    text-align: right;
    float: right;
}

.Footer .widthHolder .social a:link {
    color: #5F6060;  
    text-decoration: none;
}

.Footer .widthHolder .social a:visited {
    color: #5F6060;  
    text-decoration: none;
}
